<template>
    <global-layout>
        <el-form :model="formValidate" :rules="rules" ref="ruleForm" label-width="100px" class="article-form">
          <el-form-item label="" prop="title">
            <el-input v-model="formValidate.title" maxlength="60"></el-input>
          </el-form-item>
          <el-form-item label="" prop="content">
            <global-mavon-editor @save="saveArticleHandle" :toHtml="markDownToHtml" :text.sync="formValidate.md"></global-mavon-editor>
          </el-form-item>
          <el-form-item label="" prop="url">
            <el-input class="article-input-width" v-model="formValidate.url"></el-input>&nbsp;&nbsp;&nbsp;
            <el-link type="primary" :href="`https://www.isyxf.com/articles/${formValidate.url}`" :underline="false" target="_blank"></el-link>
          </el-form-item>
          <el-form-item label="" prop="classifyId">
            <el-select class="article-input-width" v-model="formValidate.classifyId" filterable placeholder="">
              <el-option
                v-for="item in classifyList"
                :key="item.id"
                :label="item.title"
                :value="item.id">
              </el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="" prop="tags">
            <el-checkbox-group v-model="formValidate.tags">
              <el-checkbox v-for="(tag,index) in tags" :key="index" :label="tag.id + ''" value="4">{{tag.name}}</el-checkbox>
              <el-button type="text" icon="el-icon-edit" @click="showTagDialogHandle"></el-button>
            </el-checkbox-group>
          </el-form-item>
          <el-form-item label="">
            <el-switch
              v-model="formValidate.state"
              :active-value="1"
              :inactive-value="0">
            </el-switch>
          </el-form-item>
          <el-form-item label="">
              <el-button type="primary" @click="saveArticleHandle"></el-button>
          </el-form-item>
        </el-form>
        <global-dialog-tags @tagHandle="tagHandle" ref="dialogTags"></global-dialog-tags>
    </global-layout>
</template>
<script>
import GlobalLayout from '@/components/GlobalLayout.vue'
import GlobalMavonEditor from '@/components/GlobalMavonEditor.vue'
import GlobalDialogTags from '@/components/GlobalDialogTags.vue'

export default {
  name: 'articleNew',
  components: { GlobalLayout, GlobalMavonEditor, GlobalDialogTags },
  data() {
    return {
      //  ()
      articleType: 'add',
      // 
      classifyList: [],
      // 
      tags: [],
      // 
      formValidate: {
        // id
        id: '',
        // 
        title: '',
        // 
        brief: '',
        // 
        content: '',
        //  markdown 
        md: '',
        // 
        classify: '',
        // 
        articleId: '',
        // 
        state: 0,
        // 
        tags: [],
      },
      // 
      rules: {
        title: [
          { required: true, message: '', trigger: 'blur' },
          { min: 1, max: 60, message: ' 1  60 ', trigger: 'blur' },
        ],
        md: [
          { required: true, message: '', trigger: 'blur' },
          { min: 1, max: 4000, message: '', trigger: 'blur' },
        ],
        url: [
          { required: true, message: '', trigger: 'blur' },
          { min: 3, max: 120, message: ' 3  40 ', trigger: 'blur' },
        ],
        classifyId: [
          { required: true, message: '', trigger: 'change' },
        ],
        tags: [
          { type: 'array', required: true, message: '', trigger: 'change' },
        ],
      },
    }
  },
  created() {
    this.getClassifyList()

    const { params, name } = this.$route
    if (params.id && name === 'blogArticleEdit') {
      this.articleType = 'edit'
      this.getArticleInfo(params.id)
    }
  },
  methods: {
    /**
     * 
     */
    getArticleInfo(id) {
      this.$axios.articleDetail({ id }).then((info) => {
        if (info) {
          info.tags = info.tags.split(',')
          this.formValidate = info
        } else {
          console.log('')
        }
      })
    },

    /**
     * 
     */
    tagHandle(list) {
      this.tags = list
    },

    /**
     * 
     */
    getClassifyList() {
      this.$axios.classifyList().then((list) => {
        this.classifyList = list
      })
    },

    /**
     * markDown html 
     */
    markDownToHtml(html) {
      this.formValidate.content = html
    },

    /**
     * / 
     */
    saveArticleHandle() {
      this.$refs['ruleForm'].validate((valid) => {
        let successMessage = ''

        if (!valid) {
          return false
        }

        let fn
        if (this.articleType === 'add') {
          fn = 'articleAdd'
          successMessage = ''
        } else {
          fn = 'articleEdit'
          successMessage = ''
        }

        const params = { ...this.formValidate }
        const moreText = params.content.split('<!--more-->')

        if (moreText.length < 2) {
          this.$message.error('')
          return
        }

        if (moreText[0].length > 240) {
          this.$message.error('240')
          return
        }
        params.tags = params.tags.filter((val) => val !== '').join(',')
        params.brief = `${moreText[0]}</p>`

        this.$axios[fn](params).catch((e) => {
          this.$message.error(e.message)
        }).then((data) => {
          if (data && this.articleType === 'add') {
            this.formValidate.id = data
            this.articleType = 'edit'
          }
          this.$message.success(successMessage)
        })
      })
    },

    /**
     * 
     */
    showTagDialogHandle() {
      this.$refs.dialogTags.showDialog()
    },
  },
}
</script>
<style lang="stylus">
.article-form{
  margin-top 10px
  .article-input-width{
    width 320px
  }

  .el-form-item{
    .el-form-item__content{
      text-align left
    }
  }

  .el-checkbox{
    margin-right: 16px
  }

  .el-checkbox__label{
    padding-left 4px
  }

  .el-button--text{
    color #888
  }
}
</style>
